.Cartitem {
    margin-bottom: 1em;
    display: flex;
    justify-content: space-between;
    height: 44px;
    line-height: 40px;
    border-bottom: 1px solid lightgrey;

}

.Cartitem div {
    width: 25%;
    padding: 2px;
}

.Cartitem button {
    width: 30px;
    height: 30px;
    border-radius: 6px;
    margin: 0 3px;
    border: 1px dashed lightgrey;
}

.Cartitem input {
    width: 20%;
}

.Cartitem .totals {
    text-align: right;
}
