.Home {
    height: auto;
    width: 90vw;
    margin: 5px auto;
    background: rgb(201,218,236);
    background: linear-gradient(90deg, rgba(230,230,240,0.2) 0%, rgba(255,255,255,0.6) 50%, rgba(230,230,240,0.2) 100%);
    display: flex;
    flex-direction: column;
    font-family: 'Montserrat';
    font-size: 16px;
}

.Home p {
    text-indent: 40px;
}

.Section {
    overflow-x: auto;

}

.Home .Container {
    padding: 0 60px 60px 60px;
    background: #f6f6f6;
}

.Home .Section+.Section{
    border-top: lightslategray solid 1px;
    margin-top: 30px;
}

.Section h1,h2,h3{
    text-align: center;
    font-family: 'Peddana';
    margin: 30px 0;

}

.wrappingText {
    margin-left: 40px;
}

.guitarImage {
    display: inline-block;
    margin: 0 60px 0 10px;
    width: 330px; 
    height: 500px;

}

.imgF1{
    display: block;
    margin: 0 auto; 
    width:600px;
    height:350px;

}

@media only screen and (max-width: 600px) {
    .guitarImage {
        display: block;
        width: 120px; 
        height: 200px;
        margin: 0 30px 0 0;

    }

    .wrappingText {
        margin-left: 20px;
    }
    
    .Section .imgF1{

        width: 300px;
        height: 175px;
    }

}