* {
  padding: 0;
  box-sizing: border-box;
}

a {
  color: lightcyan;
  text-decoration: none;
}

ul {
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  min-width: 420px;
  background-image: url(assets/jap_waves.png);
  background-color: #f8f8f8;
  background-repeat: repeat;
  font-family: 'Peddana';
  color: #333

}

@font-face {
  font-family: 'Peddana';
  src: local('Peddana'), url(./fonts/Peddana-Regular.ttf) format('truetype');
  src: local('Montserrat'), url(./fonts/Montserrat-Regular.ttf) format('truetype');
}

.Title {
  font-family: 'Peddana';
  width: 90vw;
  padding: 40px;
  justify-content: center;
  background: rgb(201,218,236);
  box-shadow: 0 5px 5px 5px #888888;
  background: linear-gradient(180deg, rgba(230,230,240,0.2) 0%, rgba(255,255,255,1) 50%, rgba(230,230,240,0.2) 100%);
  color: #333;
  text-align: center;
  margin: 0 auto 40px;
  border-bottom: rgb(230,230,240) solid;
  border-radius: 9px;
}

/* Style the footer */
.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  color: #333;
  background-color: rgba(132,132,132,0.8);
  padding: 10px 10px;
  font-weight: 500;
  display: flex;
  justify-content: center;
}

.footer span {
  display: inline-block;
  padding: 0 4vw;
}

.footer span+span {
  border-left: dimgrey solid 2px;

}

.Container {
  width: 70vw;
  position: relative;
  margin: 0 auto;
  padding-bottom: 40px;
}

@media only screen and (max-width: 600px) {
  .Container {
      width: 100%;
  }

  .footer span{
    padding: 0 1vw;
    font-size: 12px;
  }
}