/* Style the content */
.Storage {
    height: auto;
    width: 90vw;
    margin: 5px auto;
    background: rgb(201,218,236);
    background: linear-gradient(90deg, rgba(230,230,240,0.2) 0%, rgba(255,255,255,0.6) 50%, rgba(230,230,240,0.2) 100%);
    display: flex;
    flex-direction: column;
    text-align: center;

}

.Container span {
    border-radius: 9px;
    
}

.Storage h1 {
    min-width: 200px;
    font-size: 33;
}

.Storage p {
    height: 100px;
    padding-left: 40px;
    background: rgb(201,218,236);
    box-shadow: 2px 2px 2px 2px #d8d8d8;
    background: rgba(255, 255, 255, 0.8);
    text-align: left;
    min-width: 200px;
    font-size: 15px;
    padding: 20px;
    overflow-x: auto;
    margin-bottom: 60px;
    border-radius: 18px;
    margin: 0 auto;
}

.displayItem span{
    display: block;
    padding: 15px;
    width: 50vw;
    margin: 20px auto;
    background: rgba(100, 100, 100, 0.6);
    color: #f3f3f3;
    height: auto;
}

.displayItem {
    padding: 30px 0;
    margin: 90px 0;
    overflow-x: auto;
    background: linear-gradient(180deg, rgba(170,150,150,0.05) 0%, rgba(170,150,150,0.1) 50%, rgba(170,150,30,0.05) 100%);
    border-radius: 16px;
    border-top: #333 solid 1px;
}

@media only screen and (max-width: 600px) {
    .Storage {
        margin: 0 auto;
    }

}

