
/* Style the top navigation bar */
.Navbar {
    overflow: hidden;
    background-color: #333;
    width: 100%;
    height: 60px;
    vertical-align: middle;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

}

  /* Style the topnav links */
.Navbar a {
    font-size: 100%;
    font-family: inherit;
    width: 100px;
    height: 40px;
    line-height: 20px;
    padding: 10px;
    border: 0;
    border-radius: 6px;
    display: flex;
    justify-content: center;
    
    background-color: #333;
    color: lightgray;
    text-align: center;
    text-decoration: underline;
}

/* Change color on hover */
.Navbar a:hover {
    background-color: #151515;
}

.Navbar ul {
    height: 60px;
}

.Navbar li{
    display: inline-block;
    padding: 0 4vw;
    height: 40px;
    margin-top: 10px;
}

.Navbar li+li {
    border-left: white solid 1px;
}

