.Shoppingcart {
    width: 750px;
    margin: 20px auto;
    background: lightyellow;
    border-radius: 7px;
    box-shadow: 1px 2px 3px 0px rgba(0,0,0,0.2);
    display: flex;
    flex-direction: column;
    padding: 1em;
    font-family: 'montserat';
}

.Cart-title {
    border-bottom: 1px solid grey;
    color: grey;
    font-size: 20px;
    font-weight: 400;
    margin-bottom: 1em;
}

.Shoppingcart h4 {
    display: inline;
}

.Shoppingcart .clearButton {
    background-color: goldenrod;
    border-radius: 3px;
    color: white;
    border: none;
    width: 60px;
    height: 40px;
}

.Sc_Control {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
}
